import { _decorator, Component, Prefab, Node, Label } from 'cc';
import { oxAudio } from './oxAudio';
import { oxSlotCtrl } from './oxSlotCtrl';
import { ox } from './cOx';

import { sp } from 'cc';
import { Vec3 } from 'cc';
import { instantiate } from 'cc';
import { UITransform } from 'cc';
import { tween } from 'cc';
import { Animation } from 'cc';

import { Button } from 'cc';
import { find } from 'cc';

import { Tween } from 'cc';
import { macro } from 'cc';
import { Num } from '../../core_tgx/framework/util/Num';
import { gameNet } from '../../module_basic/scripts/NetGameServer';
import { SubGameDef } from '../../module_basic/scripts/SubGameDef';
import { ResPlaySlots } from '../../module_basic/shared/protocols/public/slots/PtlPlaySlots';
import { dataChange, oldRes } from './dataChange';
import { UserMgr } from '../../module_basic/scripts/UserMgr';
import { RoomMgr } from '../../module_basic/scripts/RoomMgr';
import { UIHelp, UIhelpParam } from '../../module_basic/ui_help/UIHelp';
import { LanguageData } from 'db://assets/module_basic/scripts/language/LanguageData';

const { ccclass, property } = _decorator;

@ccclass('oxMain')
export class oxMain extends Component {
    @property({ type: Prefab, tooltip: "滚轮角色Pb" })
    public rolePb: Prefab[] = [];
    @property({ type: Prefab, tooltip: "角色详情Pb" })
    public detailPb: Prefab = null!;
    @property({ type: Prefab, tooltip: "万能特效Pb" })
    public wildEffectPb: Prefab = null!;
    @property({ type: Node, tooltip: "中奖数字特效" })
    public effectNum: Node = null!;
    @property({ type: Node, tooltip: "中奖光特效" })
    public effectLight: Node = null!;
    @property({ type: Node, tooltip: "中奖牌特效" })
    public effectCard: Node = null!;
    @property({ type: Node, tooltip: "中奖图层遮罩" })
    public maskNode: Node = null!;
    @property({ type: Node, tooltip: "中奖图层遮罩2" })
    public maskNode2: Node = null!;
    @property({ type: Node, tooltip: "中奖特效" })
    public effectLine: Node = null!;
    @property({ type: Node, tooltip: "游戏背景节点" })
    public BgNode: Node = null!;
    @property({ type: Node, tooltip: "免费摇奖背景节点" })
    public freeBgNode: Node = null!;
    @property({ type: Node, tooltip: "免费特效节点" })
    public freeEffectNode: Node = null!;
    @property({ type: Node, tooltip: "免费元宝特效" })
    public freeCoinEffect: Node = null!;
    @property({ type: Node, tooltip: "金牛灯光背景" })
    public freeLightNode: Node = null!;
    @property({ type: Node, tooltip: "免费灯光特效" })
    public freeWheelLightEffect: Node = null!;
    @property({ type: Node, tooltip: "x10特效" })
    public free_X10Effect: Node = null!;
    @property({ type: Node, tooltip: "中奖背景2" })
    public win2BG: Node = null!;
    @property({ type: Node, tooltip: "中奖背景3" })
    public win3BG: Node = null!;
    @property({ type: Node, tooltip: "滚屏提示" })
    public tipNode: Node = null!;
    @property({ type: Node, tooltip: "中奖提示" })
    public winNode: Node = null!;
    @property({ type: Node, tooltip: "爆金币" })
    public coinBoomEffect: Node = null!;
    @property({ type: Label, tooltip: "单线中奖金币" })
    public coinLineLab: Label = null!;
    @property({ type: Node, tooltip: "公牛动画" })
    public bullAnim: Node = null!;
    @property({ type: Node, tooltip: "detailMask" })
    public deskMask: Node = null!;
    @property({ type: Node, tooltip: "slotCtrl" })
    public slotCtrlNode: Node = null!;

    /**延迟点击 */
    public delayClick: boolean = false;
    public audio: oxAudio = null!;
    public slotCtrl: oxSlotCtrl = null!;
    private bIsFreeGame: boolean = false;
    private freeTimes: number = 0;
    private bigWinBoo: boolean = false;
    private stopFree: boolean = false;
    private freeGameCoin: number = 0;

    onLoad() {
        ox.node = this;
        this.slotCtrl = this.slotCtrlNode.getComponent(oxSlotCtrl) as oxSlotCtrl;
        this.slotCtrl.initBetContent(ox.BETNUM, ox.BET, ox.LINES);
        this.audio = this.node.getComponent('oxAudio') as oxAudio;
        ox.wheelList = [];
        ox.auto = false;
        ox.autoTimes = 0;
        ox.status = 0;
        ox.winLinePos = [480, 380, 380, 380, 380, 180, 180, 180, 180, 20];
        ox.rollIndex = 0;
        ox.lotteryRes = null;
        ox.rollEnable = true;
        ox.money = UserMgr.inst.coin;
        this.slotCtrl.lblUserCoin.string = Num.exchangeToThousands(ox.exchangeRate, ox.money);
        this.delayClick = false;
        this.bIsFreeGame = false;
        this.bigWinBoo = false;
        this.freeTimes = 0;
        this.stopFree = false;
        this.freeGameCoin = 0;
        ox.money = ox.money;
    }

    initWheel() {
        for (let i = 0; i < ox.wheelList.length; i++) {
            ox.wheelList[i].initWheel();
        }
    }

    start() {
        this.initWheel();
        this.slotCtrl.setSpinAnim(0);
        this.slotCtrl.bindBet();
        //监听老牛动画播放
        let ani: sp.Skeleton = this.bullAnim.getComponent(sp.Skeleton) as sp.Skeleton;
        ani.setCompleteListener((event) => {
            switch (event.animation.name) {
                case "idle2":
                    ani.setAnimation(0, "idle", true);
                    break;
                case "wild_collect":
                    ani.setAnimation(0, "idle", true);
                    break;
                case "win":
                    ani.setAnimation(0, "idle", true);
                    break;
                case "win2":
                    ani.setAnimation(0, "idle", true);
                    break;
                case "win3":
                    ani.setAnimation(0, "rsg_win_exit", false);
                    break;
                case "rs_start":
                    ani.setAnimation(0, "rs_idle", true);
                    break;
                case "dup":
                    ani.setAnimation(0, "rs_idle", true);
                    break;
                case "rs_exit":
                    ani.setAnimation(0, "idle", true);
                    break;
                case "rsg_win_exit":
                    ani.setAnimation(0, "idle", true);
                    break;
                case "rs_win":
                    ani.setAnimation(0, "rsg_win_idle", true);
                    break;
            }
        });
    }

    update() {

    }

    onCLick(event: TouchEvent, args: any) {
        switch (args) {
            case "roll":
                this.startRoll();
                break;
            case "add":
                // this.add();
                this.add();
                break;
            case "dec":
                this.dec();
                break;
                // case "closeBigWin":
                //     this.audio.playBgm(0);
                break;
            case "lastmoney":
                ox.lastmoneyPanel.showPanel();
                break;
            case "help":
                tgx.UIMgr.inst.showUI(UIHelp, null, this, new UIhelpParam("rule"));
                // ox.rulePanel.showPanel();
                break;
            case "pay":
                tgx.UIMgr.inst.showUI(UIHelp, null, this, new UIhelpParam("payout"));
                // ox.payPanel.showPanel();
                break;
            case "exitGame":
                this.onBtnExitClicked();
                break;
        }
    }

    onBtnExitClicked() {
        //this.node.getParent().getChildByName('Machine').getComponent(AudioSource).stop();
        tgx.UIAlert.show(LanguageData.getLangByID("exit_game"), true).onClick(async (ok: Boolean) => {
            if (ok) {
                ox.node.close();
                RoomMgr.inst.exitRoom();
                //tgx.SceneUtil.loadScene(Slots4GameMgr.inst.entryScene);
            }
        });
    }

    startRoll() {
        if (this.freeTimes > 0 || this.bigWinBoo || this.stopFree || this.bIsFreeGame || this.delayClick || ox.rollEnable == false) {
            return;
        }
        if (!ox.auto) {
            this.delayClick = true;
            this.scheduleOnce(() => {
                this.delayClick = false;
            }, 1);
            if (ox.status == 0) {
                ox.status = 2;
                this.sendRoll();
            } else if (ox.status == 1) {
                this.slotCtrl.setSpinAnim(3);
                this.stopImmediately();
            }
        }
    }

    add() {
        if (this.freeTimes > 0 || this.bigWinBoo || this.stopFree || ox.auto) {
            return;
        }
        ox.sumIdx += 1;
        ox.sumIdx = this.slotCtrl.updateBet(ox.sumIdx) == 0 ? ox.sumIdx - 1 : ox.sumIdx;
    }

    dec() {
        if (this.freeTimes > 0 || this.bigWinBoo || this.stopFree || ox.auto) {
            return;
        }
        ox.sumIdx -= 1;
        ox.sumIdx = this.slotCtrl.updateBet(ox.sumIdx) == 0 ? ox.sumIdx + 1 : ox.sumIdx;
    }

    startAuto(n: number) {
        if (this.freeTimes > 0 || this.bigWinBoo || this.stopFree || this.bIsFreeGame || this.delayClick) {
            return;
        }
        if (ox.status == 0) {
            ox.auto = true;
            ox.autoTimes = n;
            (((this.slotCtrl.Btn_stopAuto as Node).children[0] as Node).getComponent(Label) as Label).string = n + "";
            if (n > 999) {
                ((this.slotCtrl.Btn_stopAuto as Node).children[0] as Node).scale = new Vec3(0.76, 0.76, 0.76);
            } else if (n > 99) {
                ((this.slotCtrl.Btn_stopAuto as Node).children[0] as Node).scale = new Vec3(0.9, 0.9, 0.9);
            }
            else {
                ((this.slotCtrl.Btn_stopAuto as Node).children[0] as Node).scale = new Vec3(1, 1, 1);
            }
            this.slotCtrl.Btn_stopAuto.active = ox.auto;
            this.slotCtrl.Btn_start.active = !this.slotCtrl.Btn_stopAuto.active;
            this.scheduleOnce(this.sendRoll, 1);
        }
    }

    stopAuto() {
        ox.auto = false;
        ox.autoTimes = 0;
    }

    private stateCallBackBn: boolean = false;
    stateCallBack() {
        let st = 0;
        for (let i in ox.wheelList) {
            if (ox.wheelList[i].status) {
                st = 1;
                break;
            }
        }
        ox.status = st;
        if (ox.status == 0) {
            if (this.stateCallBackBn) {
                return;
            }
            this.stateCallBackBn = true;
            this.scheduleOnce(() => {
                this.stateCallBackBn = false;
            }, 1)
            //结束当前轮盘
            if (ox.lotteryRes.winscore > 0) {
                this.slotCtrl.setSpinAnim(3);
            } else {
                this.slotCtrl.setSpinAnim(2);
            }
            if (!this.bIsFreeGame) {
                //延迟一点让立即结束落位
                this.scheduleOnce(() => {
                    //万能牌特效
                    for (let i in ox.wheelList) {
                        let n = 4;
                        if (Number(i) == 1) {
                            n = 5;
                        }
                        for (let j = 0; j <= n - 2; j++) {
                            if (ox.wheelList[i].roleIdList[j + 1] == 7) {
                                ox.wheelList[i].rolePbList[j + 1].children[0].getComponent(sp.Skeleton).setAnimation(0, "spawn", true);
                                let pb: Node = instantiate(this.wildEffectPb) as Node;
                                let transForm: UITransform = ox.wheelList[i].rolePbList[j + 1].getComponent(UITransform) as UITransform;
                                let newVec3: Vec3 = transForm.convertToWorldSpaceAR(new Vec3(0, 0, 0));
                                transForm = this.node.getComponent(UITransform) as UITransform;
                                let p = transForm.convertToNodeSpaceAR(newVec3);
                                pb.position = p;
                                this.node.addChild(pb);
                                tween(pb)
                                    .delay(0.5)
                                    .call(() => {
                                        this.audio.playParticalStart();
                                        (this.bullAnim as Node).getComponent(sp.Skeleton)?.setAnimation(0, "wild_collect", false);
                                    })
                                    .to(0.3, { position: new Vec3(202, -202, 0) })
                                    .call(() => {
                                        this.audio.playWildOx();
                                        this.audio.playParticalEnd();
                                        pb.parent = null;
                                    })
                                    .start();
                            }
                        }
                    }
                }, 0.1);
            } else {
                ox.rollIndex++;
                if (ox.rollIndex < ox.lotteryRes.getBigWin.res_list.length) {
                    this.bigWinRollNext(ox.lotteryRes.getBigWin.res_list[ox.rollIndex].nHandCards);
                }
            }

            let rIndex = ox.lotteryRes.getBigWin.res_list.length;

            if (this.bIsFreeGame) {
                this.freeGameCoin += ox.lotteryRes.winscore;
            }
            let freeFlag = ox.lotteryRes.getFreeTime.bFlag
            this.scheduleOnce(() => {
                if (rIndex == ox.rollIndex || rIndex == 1) {
                    this.playWinAnim(freeFlag);
                }
                if (!ox.auto) {

                }
            }, 1);

            if (ox.lotteryRes.getFreeTime.bFlag) {
                if (this.freeTimes == 0) {
                    ox.auto = false;
                    this.freeTimes = ox.lotteryRes.getFreeTime.nFreeTime;
                    this.freeTimes--;
                    this.scheduleOnce(() => {
                        this.closeShine();
                        this.startFreeGame();
                    }, 2);
                } else {
                    this.freeTimes = ox.lotteryRes.getFreeTime.nFreeTime;
                    this.stopFree = false;
                }
            }
        }
    }

    //免费次数有关
    startFreeGame() {
        console.log("startFreeGame");
        this.audio.playBgm();
        this.freeGameCoin = 0;
        this.bIsFreeGame = true;
        this.freeBgNode.active = true;
        ox.auto = true;
        this.sendRoll();
    }

    stopFreeTimes() {
        console.log("stopFreeTimes freeGameCoin : ", this.freeGameCoin);
        this.audio.playBgm();
        ox.auto = false;

        this.scheduleOnce(() => {
            this.freeBgNode.active = false;
            this.bIsFreeGame = false;
        }, 2);
    }

    private showLine: Function = null!;
    playWinAnim(freeTimeFlag: boolean) {
        //动画结束后自动roll
        let hasWinBool = 0;
        let allLine = [];

        for (let i in ox.lotteryRes.nWinCards) {
            if (ox.lotteryRes.nWinCards[i]) {
                allLine.push(i);
            }
        }
        let lines = ox.lotteryRes.nWinLinesDetail;
        let rIndex = ox.rollIndex;
        let list = (this.freeTimes > 0 || this.stopFree) ? [allLine] : [allLine, ...lines];
        hasWinBool = list.length - 1;
        if (ox.lotteryRes.winscore > 0) {
            if (ox.lotteryRes.isAllWin) {
                this.showAllWin();
            } else {
                this.showWin();
            }
        } else {
            this.slotCtrl.setSpinAnim(0);
            this.scheduleOnce(() => {
                this.slotCtrl.setSpinAnim(0);
                if (ox.auto && ox.autoTimes > 0) {
                    this.scheduleOnce(this.sendRoll, 1.5);
                }
            }, 1);
        }
        let animIndex = 0;
        this.showLine = function () {
            if (rIndex == ox.rollIndex) {
                this.closeShine();
                for (let i: number = 0; i < 12; i++) {
                    this.closeAnim(i % 3, parseInt(i / 3 + ""));
                }
                if (!!!list[animIndex] || list[animIndex].length == 0) {
                    return;
                }
                this.maskNode.active = true;
                for (let j in list[animIndex]) {
                    this.showAnim(list[animIndex][j] % 3, parseInt(list[animIndex][j] / 3 + ""));
                }
                if (animIndex == 0) {
                    this.audio.playWinFirst();
                    for (let i = 0; i < ox.lotteryRes.nWinLines.length; i++) {
                        this.effectLine.children[ox.lotteryRes.nWinLines[i]].active = true;
                        this.effectNum.children[ox.lotteryRes.nWinLines[i]].active = true;
                    }
                } else {
                    this.audio.playWinLoop();
                    //中奖数字和位置
                    if (ox.lotteryRes.nWinLines.length > 0) {
                        this.coinLineLab.node.active = true;
                        this.coinLineLab.node.y = ox.winLinePos[ox.lotteryRes.nWinLines[animIndex - 1]];
                        this.coinLineLab.string = Num.fixNum(ox.exchangeRate, ox.lotteryRes.nWinDetail[animIndex - 1]);
                        this.effectLine.children[ox.lotteryRes.nWinLines[animIndex - 1]].active = true;
                        this.effectNum.children[ox.lotteryRes.nWinLines[animIndex - 1]].active = true;
                    }
                }
                animIndex++;
                if (animIndex == ox.lotteryRes.nWinLines.length + 1) {
                    animIndex = 0;
                }
            }
        }
        if (ox.auto) {
            this.schedule(this.showLine, 3, 1, 0.01);
        } else {
            this.schedule(this.showLine, 3, macro.REPEAT_FOREVER, 0.01);
        }
        if (ox.lotteryRes.winscore <= ox.betSum * 20) {
            this.slotCtrl.setSpinAnim(0);
        }

        this.scheduleOnce(() => {
            this.tipNode.active = true;
            this.winNode.active = false;
            this.win2BG.active = false;
            this.win3BG.active = false;
            if (this.stopFree) {
                this.stopFree = false;
                this.stopFreeTimes();
                this.closeShine();
            }
            if (this.freeTimes > 0 && !freeTimeFlag) {
                this.freeTimes--;
                // ((((this.slotCtrl as oxSlotCtrl).Btn_free as Node).getChildByName('auto_lab') as Node).getComponent(Label) as Label).string = this.freeTimes;
                if (this.freeTimes == 0) {
                    this.stopFree = true;
                }
            }
        }, hasWinBool > 0 ? hasWinBool * 3 : 1);
    }

    showAllWin() {
        let coin: number = ox.lotteryRes.winscore / 10;
        this.slotCtrl.playAnimWin(-1, coin);
        this.showWinNode(coin);
        let x10: Node = instantiate(this.free_X10Effect);
        x10.active = true;
        x10.parent = this.winNode.parent;
        x10.y = x10.y + 200;
        x10.scale = new Vec3(0.1, 0.1);
        x10.opacity = 255;
        tween(x10)
            .to(0.6, { scale: new Vec3(1, 1), y: 313 })
            .delay(0.3)
            .to(0.6, { scale: new Vec3(0, 0), y: this.winNode.y })
            .call(() => {
                this.winNodeJump();
            })
            .delay(1)
            .call(() => {
                x10.parent = null;

                this.showWin();
            })
            .start();
    }

    winNodeJump() {
        let jump: Node = find("infoboard_a/jump", this.node) as Node;
        jump.active = true;
        jump.getComponent(Animation)?.play();
        tween(this.winNode)
            .to(0.3, { scale: new Vec3(1.3, 1.3) })
            .to(0.3, { scale: new Vec3(1, 1) })
            .delay(0.4)
            .call(() => {
                jump.active = false;
            })
            .start();
    }

    showWinNode(coin: number) {
        this.tipNode.active = false;
        this.winNode.active = true;
        this.winNode.children[1].active = true;
        this.winNode.children[2].active = false;
        if (coin < ox.betSum * 5) {
            this.audio.playWinOx();
            this.win2BG.active = true;
            this.win2BG.children[2].getComponent(Animation)?.play();
            this.win3BG.active = false;
        } else {
            this.audio.playMegaWinOx();
            this.win2BG.active = false;
            this.win3BG.active = true;
            this.win3BG.children[2].getComponent(Animation)?.play();
        }
    }
    showWin() {
        if (!this.bIsFreeGame) {
            this.audio.playBW();
            //判断播放金币掉落动画
            if (ox.lotteryRes.winscore > ox.betSum * 20) {
                this.slotCtrl.playBigWinCoin(ox.lotteryRes.winscore);
                // this.slotCtrl.playAnimWin(1, ox.lotteryRes.winscore);
                //播放大奖声音
                this.audio.playbigwinmain();
            } else if (ox.lotteryRes.winscore > ox.betSum * 5) {
                this.slotCtrl.playAnimWin(0, ox.lotteryRes.winscore);
            } else {
                this.slotCtrl.playAnimWin(-1, ox.lotteryRes.winscore);
            }
            this.showWinNode(ox.lotteryRes.winscore);
            if (ox.lotteryRes.winscore < ox.betSum * 3) {
                this.bullAnim.getComponent(sp.Skeleton)?.setAnimation(0, "win", false);
            } else {
                this.bullAnim.getComponent(sp.Skeleton)?.setAnimation(0, "win2", false);
            }
        } else {
            //判断播放金币掉落动画
            if (ox.lotteryRes.winscore > ox.betSum * 20) {
                this.slotCtrl.playBigWinCoin(ox.lotteryRes.winscore);
                // this.slotCtrl.playAnimWin(1, ox.lotteryRes.winscore);
                //播放大奖声音
            } else if (ox.lotteryRes.winscore > ox.betSum * 5) {
                this.slotCtrl.playAnimWin(0, ox.lotteryRes.winscore);
            } else {
                this.slotCtrl.playAnimWin(-1, ox.lotteryRes.winscore);
            }
            this.audio.playBigWinOx();
            this.showWinNode(ox.lotteryRes.winscore);
            if (ox.lotteryRes.winscore < ox.betSum * 3) {
                (this.bullAnim as Node).getComponent(sp.Skeleton)?.setAnimation(0, "win3", false);
                this.exitBigWin(false);
            } else {
                (this.bullAnim as Node).getComponent(sp.Skeleton)?.setAnimation(0, "rs_win", false);
                this.exitBigWin(true);
            }
        }
    }
    closeBigWin() {
        this.slotCtrl.playAnimWin(-1, ox.lotteryRes.winscore);
        this.showWinNode(ox.lotteryRes.winscore);
        if (ox.auto && ox.autoTimes > 0) {
            this.scheduleOnce(this.sendRoll, 1);
        }
    }

    //退出大奖模式
    exitBigWin(isBigWin: boolean) {
        let bg: Node = find("bg", this.node) as Node;
        tween(bg)
            .to(0.5, { scale: new Vec3(1, 1, 1) })
            .call(() => {
                this.audio.playBigWinExitOx();
                this.freeLightNode.active = false;
                this.freeWheelLightEffect.active = false;
                this.bIsFreeGame = false;
                ((this.freeEffectNode as Node).getChildByName("MF_txt") as Node).active = false;
                this.freeBgNode.active = false;
                ((this.freeEffectNode as Node).getChildByName("MF_fastspin") as Node).active = false;
                this.maskNode2.active = false;
                if (isBigWin) {
                    (this.bullAnim as Node).getComponent(sp.Skeleton)?.setAnimation(0, "rsg_win_exit", false);
                }
            })
            .start();
        let effectNode: Node = find("effectNode", this.node) as Node;
        tween(effectNode)
            .to(0.5, { scale: new Vec3(1, 1, 1) })
            .call(() => {
                this.audio.playBgm();
            })
            .start();
    }

    //0-5 0-2
    showAnim(cols: number, index: number) {
        let targetIdx = index + 1;
        for (let i in ox.wheelList[cols].rolePbList[targetIdx].children) {
            ox.wheelList[cols].rolePbList[targetIdx].children[i].active = false;
        }

        //添加结束
        let nodeList = this.effectLight.children;
        let posIndex: number = cols * 4 + index;
        nodeList[posIndex].active = true;
        let nodeList2 = this.effectCard.children;
        for (let i = 0; i < nodeList2[posIndex].children.length; i++) {
            let item: Node = nodeList2[posIndex].children[i];
            if (i == (ox.wheelList[cols].roleIdList[targetIdx] - 1)) {
                item.active = true;
                let ani: sp.Skeleton = item.getComponent(sp.Skeleton) as sp.Skeleton;
                ani.setCompleteListener((event) => {
                    switch (event.animation.name) {
                        case "win":
                            ani.setAnimation(0, "win_bg", true);
                            break;
                    }
                })
                ani.setAnimation(0, "win", false);
            } else {
                item.active = false;
            }
        }
    }

    closeAnim(cols: number, index: number) {
        let targetIdx = index + 1;
        // for (let i in ox.wheelList[cols].rolePbList[targetIdx].children) {
        //     ox.wheelList[cols].rolePbList[targetIdx].children[i].active = true;
        // }
        ox.wheelList[cols].rolePbList[targetIdx].children[0].active = true;

        //添加结束
        let nodeList = this.effectLight.children;
        nodeList[cols * 4 + index].active = false;

        let nodeList2 = this.effectCard.children;
        for (let i = 0; i < nodeList2[cols * 4 + index].children.length; i++) {
            nodeList2[cols * 4 + index].children[i].active = false;
        }
    }

    roll(list: any[]) {
        ox.status = 1;
        let line: any[] = [];
        for (let i = 0; i < 3; i++) {
            line[i] = [];
        }
        for (let i in list) {
            line[Number(i) % 3][3 - parseInt(Number(i) / 3 + "")] = list[i];
        }

        //判断是否进大奖
        if (ox.lotteryRes.getBigWin.bFlag) {
            let bigWinResult = ox.lotteryRes.getBigWin;
            this.bIsFreeGame = true;
            this.scheduleOnce(() => {
                this.bullAnim.getComponent(sp.Skeleton)?.setAnimation(0, "rs_start", false);
                this.audio.playBigWinInOx();
                if (bigWinResult.isStart) {
                    //成功触发大奖
                    this.freeCoinEffect.getComponent(Animation)?.play("mf_coin2");
                    let bg: Node = find("bg", this.node) as Node;
                    tween(bg)
                        .to(1, { scale: new Vec3(0.9, 0.9, 0.9) })
                        .call(() => {
                            this.audio.playJumpOx();
                            (this.bullAnim as Node).getComponent(sp.Skeleton)?.setAnimation(0, "dup", false);
                            this.freeLightNode.active = true;
                            this.freeWheelLightEffect.active = true;
                            (this.freeWheelLightEffect as Node).getComponent(Animation)?.play();
                        })
                        .delay(0.7)
                        .call(() => {
                            this.audio.playBgm();
                            this.audio.playBigWinTitle();
                            ((this.freeEffectNode as Node).getChildByName("MF_txt") as Node).active = true;
                            ((this.freeEffectNode as Node).getChildByName("MF_txt") as Node).getComponent(Animation)?.play("MF_txt_in");
                        })
                        .delay(0.42)
                        .call(() => {
                            ((this.freeEffectNode as Node).getChildByName("MF_txt") as Node).getComponent(Animation)?.play("MF_txt_loop");
                        })
                        .delay(1.2)
                        .call(() => {
                            this.freeBgNode.active = true;
                        })
                        .start()
                    let effectNode: Node = find("effectNode", this.node) as Node;
                    tween(effectNode)
                        .delay(0.3)
                        .to(0.5, { position: new Vec3(0, -25, 0), scale: new Vec3(0.9, 0.9, 0.9) })
                        .start();
                } else {
                    //失败退出
                    this.freeCoinEffect.getComponent(Animation)?.play("mf_coin");
                    let bg: Node = find("bg", this.node) as Node;
                    tween(bg)
                        .delay(0.3)
                        .to(1, { scale: new Vec3(0.95, 0.95, 0.95) })
                        .call(() => {
                            (this.bullAnim as Node).getComponent(sp.Skeleton)?.setAnimation(0, "rs_exit", false);
                        })
                        .to(0.5, { scale: new Vec3(1, 1, 1) })
                        .call(() => {
                            this.bIsFreeGame = false;
                        })
                        .start()
                    let effectNode: Node = find("effectNode", this.node) as Node;
                    tween(effectNode)
                        .to(1, { scale: new Vec3(0.95, 0.95, 0.95) })
                        .to(0.5, { scale: new Vec3(1, 1, 1) })
                        .call(() => {
                            this.audio.playBgm();
                        })
                        .start()
                }
            }, 0.5);
            if (!ox.lotteryRes.getBigWin.isStart) {
                for (let i: number = 0; i < ox.wheelList.length; i++) {
                    ox.wheelList[i].startFreeRoll(...line[i]);
                }
            } else {
                for (let i: number = 0; i < ox.wheelList.length; i++) {
                    ox.wheelList[i].FreeRoll(...line[i]);
                }
                setTimeout(() => {
                    this.play_middleWheelAnim();
                }, 1000);
            }
        } else {
            if (!this.bIsFreeGame) {
                for (let i: number = 0; i < ox.wheelList.length; i++) {
                    ox.wheelList[i].startRoll(...line[i]);
                }
            } else {
                for (let i: number = 0; i < ox.wheelList.length; i++) {
                    ox.wheelList[i].FreeRoll(...line[i]);
                }
                setTimeout(() => {
                    this.play_middleWheelAnim();
                }, 1000);
            }
        }
    }

    bigWinRollNext(list: any[]) {
        ox.status = 1;
        let line: any[] = [];
        for (let i = 0; i < 3; i++) {
            line[i] = [];
        }
        for (let i in list) {
            line[Number(i) % 3][3 - parseInt(Number(i) / 3 + "")] = list[i];
        }
        for (let i: number = 0; i < ox.wheelList.length; i++) {
            ox.wheelList[i].FreeRoll(...line[i]);
        }
        setTimeout(() => {
            this.play_middleWheelAnim();
        }, 1000);
    }

    closeShine() {
        for (let i = 0; i < this.effectLine.children.length; i++) {
            this.effectLine.children[i].active = false;
        }

        for (let i = 0; i < this.effectNum.children.length; i++) {
            this.effectNum.children[i].active = false;
        }

        for (let i = 0; i < this.effectLight.children.length; i++) {
            this.effectLight.children[i].active = false;
        }

        for (let i = 0; i < this.effectCard.children.length; i++) {
            for (let j = 0; j < this.effectCard.children[i].children.length; j++) {
                this.effectCard.children[i].children[j].active = false;
            }
        }

        for (let i = 0; i < ox.wheelList.length; i++) {
            for (let j = 0; j < ox.wheelList[i].rolePbList.length; j++) {
                if (ox.wheelList[i].roleIdList[j] != 7) {
                    ox.wheelList[i].rolePbList[j].children[0].active = true;
                }
            }
        }
        this.maskNode.active = false;
        this.maskNode2.active = false;
        this.coinLineLab.node.active = false;
    }

    sendRoll() {
        if (this.slotCtrl.playOverBn == false) {
            return;
        }
        this.stateCallBackBn = false;
        this.unschedule(this.showLine);
        if (ox.detail) {
            if (this.deskMask) {
                this.deskMask.active = false;
            }
            ox.detail.node.active = false;
            ox.detail = null;
        }
        if (this.winNode.active) {
            this.tipNode.active = true;
        }
        this.winNode.active = false;
        this.win2BG.active = false;
        this.win3BG.active = false;
        ox.rollIndex++;
        this.closeShine();
        this.slotCtrl.unscheduleAllCallbacks();
        this.tipNode.active = true;
        this.winNode.active = false;
        this.win2BG.active = false;
        this.win3BG.active = false;
        this.slotCtrl.unscheduleAllCallbacks();
        ox.autoTimes = ox.autoTimes > 0 ? ox.autoTimes - 1 : 0;
        if (ox.autoTimes == 0) {
            ox.auto = false;
            ox.rollEnable = true;
        }
        // this.slotCtrl.Btn_stopAuto.active = (this.auto && this.freeTimes == 0);
        tween(this.node)
            .delay(0.1)
            .call(() => {
                this.slotCtrl.setSpinAnim(1);
            })
            .start()
        let autoTimesLab: Label = this.slotCtrl.Btn_stopAuto.children[0].getComponent(Label) as Label;
        autoTimesLab.string = ox.autoTimes + "";
        this.audio.playclickstart();
        if (ox.autoTimes > 999) {
            this.slotCtrl.Btn_stopAuto.children[0].scale = new Vec3(0.76, 0.76, 0.76);
        } else if (ox.autoTimes > 99) {
            this.slotCtrl.Btn_stopAuto.children[0].scale = new Vec3(0.9, 0.9, 0.9);
        }
        else {
            this.slotCtrl.Btn_stopAuto.children[0].scale = new Vec3(1, 1, 1);
        }

        if (!this.bIsFreeGame) {
            for (let i in ox.wheelList) {
                ox.wheelList[i].rollLoop();
            }
        }

        this.sendLottery();
    }

    async sendLottery() {
        let retSpin = await gameNet.playSlots(SubGameDef.SLOTS_OX, ox.betSum.toString());
        console.log('retSpin is', retSpin);
        let data = this.exchange(retSpin.res);
        console.log("data is ", data);
        this.lotteryBack(data);
    }

    exchange(data: ResPlaySlots) {
        let res = dataChange.change(data as ResPlaySlots);
        // ox.lotteryRes = res;
        let mydata = res;
        return mydata;
    }

    private lotteryBack(data: oldRes) {
        console.log('lotteryResult:', data);

        if (data && data.code == 0) {
            ox.lotteryRes = data;
            ox.rollIndex = 0;
            ox.money = data.userscore;
            this.slotCtrl.lblUserCoin.string = Num.exchangeToThousands(ox.exchangeRate, ox.money - data.winscore);
            // this.slotCtrl.winCoin_lab.string = Num.exchangeToThousands(ox.exchangeRate, data.winscore);
            tween(this.node)
                .delay(0.4)
                .call(() => {
                    if (ox.lotteryRes.getBigWin.bFlag) {
                        this.roll(ox.lotteryRes.getBigWin.res_list[ox.rollIndex].nHandCards);
                    } else {
                        this.roll(data.nHandCards);
                    }

                })
                .start()
        }
    }

    stopImmediately() {
        if (!ox.auto) {
            for (let i in ox.wheelList) {
                ox.wheelList[i].stopImmediately();
            }
        }
    }


    //免费模式点亮中间转轴
    play_middleWheelAnim() {
        this.scheduleOnce(() => {
            this.audio.playBigWinFastSpin();
            ((this.freeEffectNode as Node).getChildByName("MF_fastspin") as Node).active = true;
            ((this.freeEffectNode as Node).getChildByName("MF_fastspin") as Node).getComponent(Animation)?.play();
            this.maskNode2.active = true;
            this.playX10();
            (this.free_X10Effect as Node).getComponent(Animation)?.play();
            let rx = Math.random() * 16 - 8;
            let ry = Math.random() * 16 - 8;
            tween(this.node)
                .to(0.06, { position: new Vec3(rx, ry, 0) })
                .to(0.06, { position: new Vec3(-rx, -ry, 0) })
                .union()
                .repeat(20)
                .start()

        }, 5);
    }

    playX10() {
        this.free_X10Effect.active = true;
        if (this.isX10()) {

        } else {
            this.scheduleOnce(() => {
                this.free_X10Effect.active = false;
            }, 3.5);
        }
    }

    isX10(): boolean {
        let nHandCards: number[];
        if (ox.lotteryRes.getBigWin.res_list.length > 1) {
            nHandCards = ox.lotteryRes.getBigWin.res_list[ox.rollIndex].nHandCards;
        } else {
            nHandCards = ox.lotteryRes.nHandCards;
        }
        let x10Num: number = 7;
        for (let i: number = 0; i < nHandCards.length; i++) {
            let num: number = nHandCards[i];
            if (num != 7) {
                if (x10Num == 7) {
                    x10Num = num;
                } else {
                    if (x10Num != num) {
                        return false;
                    }
                }
            }
        }
        return true;
    }

    close() {
        this.audio.clearAll();
    }
};



