import { _decorator, Component, AudioClip } from 'cc';
import { ModuleDef } from '../../scripts/ModuleDef';

const { ccclass, property } = _decorator;

@ccclass('oxAudio')
export class oxAudio extends Component {
    @property([AudioClip])
    public bgmAudio: AudioClip[] = [];
    @property({ type: AudioClip, tooltip: "粒子触发" })
    public particalStart: AudioClip = null!;
    @property({ type: AudioClip, tooltip: "粒子结束" })
    public particalEnd: AudioClip = null!;
    @property({ type: [AudioClip], tooltip: "普通中奖牛语音" })
    public oxWinEf: AudioClip[] = [];
    @property({ type: [AudioClip], tooltip: "高倍中奖牛语音" })
    public oxMegaWinEf: AudioClip[] = [];
    @property({ type: AudioClip, tooltip: "出现wild牛语音" })
    public oxWildEf: AudioClip = null!;
    @property({ type: AudioClip, tooltip: "牛跳起语音" })
    public oxJumpEf: AudioClip = null!;
    @property({ type: AudioClip, tooltip: "大奖进入语音" })
    public oxBigWinInEf: AudioClip = null!;
    @property({ type: AudioClip, tooltip: "大奖中奖语音" })
    public oxBigWinEf: AudioClip = null!;
    @property({ type: AudioClip, tooltip: "大奖退出音效" })
    public BigWinExitEf: AudioClip = null!;
    @property({ type: AudioClip, tooltip: "大奖标题出现音效" })
    public BigWinTitleEf: AudioClip = null!;
    @property({ type: AudioClip, tooltip: "大奖加速旋转音效" })
    public BigWinFastSpinEf: AudioClip = null!;
    @property({ type: AudioClip, tooltip: "旋转按钮按下音效" })
    public spinEf: AudioClip = null!;
    @property({ type: AudioClip, tooltip: "滚轮旋转" })
    public wheelspin: AudioClip = null!;
    @property({ type: AudioClip, tooltip: "stopWheel" })
    public stopWheel: AudioClip = null!;
    @property({ type: AudioClip, tooltip: "bigWinEf" })
    public bigWinEf: AudioClip = null!;
    @property({ type: AudioClip, tooltip: "中奖首次音效" })
    public WinFirstEf: AudioClip = null!;
    @property({ type: AudioClip, tooltip: "中奖循环音效" })
    public WinLoopEf: AudioClip = null!;
    @property({ type: AudioClip, tooltip: "点击旋转" })
    public clickstart: AudioClip = null!;
    @property({ type: AudioClip, tooltip: "点击打开" })
    public clickOpen: AudioClip = null!;
    @property({ type: AudioClip, tooltip: "点击关闭" })
    public clickClose: AudioClip = null!;

    onLoad() {
       
    }

    start() {
       this.playBgm();
    }

    stopAudio() {
        // AudioMgr.inst.stop(); 
    }

    private BWurl:string = "sound/bgm_bigwin_main";
    playbigwinmain() {
        // AudioMgr.inst.playMusic(this.BWurl,EGameName.ox);
    }

    stopbigwinmain() {
        this.playBgm(); 
    }

    private url:string = "sound/bgm_mg";
    playBgm() {
        // AudioMgr.inst.stop();
        // AudioMgr.inst.playMusicLoop(this.url,EGameName.ox)
        tgx.AudioMgr.inst.audio.playMusicLoop(this.url, ModuleDef.SLOTS_OX)
    }

    playSpin() {
        this.playEf(this.spinEf); 
    }

    playwheelspin() {
        this.playEf(this.wheelspin); 
    }

    playParticalStart() {
        this.playEf(this.particalStart); 
    }

    playParticalEnd() {
        this.playEf(this.particalEnd); 
    }

    playWinOx() {
        let idx = Math.floor(Math.random() * this.oxWinEf.length); 
        this.playEf(this.oxWinEf[idx]); 
    }

    playMegaWinOx() {
        let idx = Math.floor(Math.random() * this.oxMegaWinEf.length); 
        this.playEf(this.oxMegaWinEf[idx]); 
    }

    playWildOx() {
        this.playEf(this.oxWildEf); 
    }

    playJumpOx() {
        this.playEf(this.oxJumpEf); 
    }

    playBigWinInOx() {
        this.playEf(this.oxBigWinInEf); 
    }

    playBigWinOx() {
        this.playEf(this.oxBigWinEf); 
    }

    playBigWinExitOx() {
        this.playEf(this.BigWinExitEf); 
    }

    playBigWinTitle() {
        this.playEf(this.BigWinTitleEf); 
    }

    playBigWinFastSpin() {
        this.playEf(this.BigWinFastSpinEf); 
    }

    playStopWheel() {
        this.playEf(this.stopWheel); 
    }

    playBW() {
        this.playEf(this.bigWinEf); 
    }

    playWinFirst() {
        this.playEf(this.WinFirstEf); 
    }

    playWinLoop() {
        this.playEf(this.WinLoopEf); 
    }

    playclickstart() {
        this.playEf(this.clickstart); 
    }

    playEf(clip: AudioClip) {
        // AudioMgr.inst.playEffect(clip);
        tgx.AudioMgr.inst.audio.playEffect(clip);
    }

    playclickopen() {
        this.playEf(this.clickOpen); 
    }

    playclickclose() {
        this.playEf(this.clickClose); 
    }

    clearAll(){
        tgx.AudioMgr.inst.audio.clearAll();
    }

}